import React, {
  useState,
  ReactNode,
  useEffect,
  FC
} from 'react';
import styled from 'styled-components';
import {
  JSXElementConstructor
} from '../../../../../../../../../Applications/IntelliJ IDEA.app/Contents/plugins/JavaScriptLanguage/jsLanguageServicesImpl/external/react';

type sizes = 'xs' | 'sm' | 'md' | 'lg' | 'xl';

export interface ButtonProps {
  children: ReactNode | string;
  onClick?: () => void;
  color?: string;
  size?: sizes;
  radius?: sizes;
  leftIcon?: ReactNode;
  rightIcon?: ReactNode;
  padding?: sizes;
  style?: React.CSSProperties;
}

export interface ButtonStyleProps {
  color?: string;
  radiusValue?: string;
  size?: string;
  buttonSize?: string;
  padding?: string;
}

const StyledButton: JSXElementConstructor<any> = styled.button<ButtonStyleProps>`
    border: none;
    padding: ${({padding}) => padding ? padding : '5px 20px' }}'5px 20px';
    text-align: center;
    font-weight: 600;
    wrap: nowrap;
    width: auto;
    color: white;
    background-color: ${({color}) => color ? color : 'red'};
    border-radius: ${({radiusSize}) => radiusSize ? radiusSize : '4px'};
    ${({buttonSize}) => buttonSize ? buttonSize : 'sm'};
  `;

const Button: FC<ButtonProps> = ({
  children,
  radius,
  color,
  rightIcon,
  onClick,
  size,
  padding,
  style,
  ...props
}: ButtonProps) => {

  const buttonSize =
    size === 'xs'
      ? `
        height: 30px;
        font-size: 12px;
        `
      : size === 'sm'
        ? `
          height: 36px;
          font-size: 14px;
          `
        : size === 'md'
          ? `
            height: 42px;
            font-size: 16px;
            `
          : size === 'lg'
            ? `
              height: 50px;
              font-size: 18px;
              `
            : `
              height: 56px;
              font-size: 20px;
              `;

  const radiusSize =
    radius === 'xs'
      ? '2px'
      : radius === 'sm'
        ? '4px'
        : radius === 'md'
          ? '8px'
          : radius === 'lg'
            ? '16px'
            : '32px';

  return (
    <StyledButton
      style={style}
      color={color}
      buttonSize={buttonSize}
      radiusSize={radiusSize}
      onClick={onClick}
      padding={padding}
    >
      {children}
    </StyledButton>
  )
}

export default Button;
